import React from 'react';
import { AiFillMinusCircle, AiFillPlusCircle } from "react-icons/ai";
import '../css/Giohang.css';
import Footer from './Footer';
import { Link } from 'react-router-dom';
import { useDispatch, useSelector } from "react-redux";
import * as action from '../redux/actions';
import { cartData$, sanPhamState$, checkPay$, dataAccount$ } from '../redux/selectors';
import { useState } from 'react';
import { AiOutlinePlusCircle, AiOutlineMinusCircle } from 'react-icons/ai';
import Button from '@mui/material/Button';
import Cards from "./Cards";
import MultiItemCrousel from "./MultiItemCrousel";
import { _idAccount$ } from "../redux/selectors";

import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import Slide from '@mui/material/Slide';
import TextField from "@mui/material/TextField";
import MenuItem from "@mui/material/MenuItem";
import Box from "@mui/material/Box";
import FormControl from "@mui/material/FormControl";
import InputLabel from "@mui/material/InputLabel";
import Select from "@mui/material/Select";

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

//địa chỉ
const tinh_tp = require("./datapicker/tinh_tp.json");
const quan_huyen = require("./datapicker/quan_huyen.json");
const xa_phuong = require("./datapicker/xa_phuong.json");
const PayScreen = () => {
    const [open, setOpen] = React.useState(false);
    const [open2, setOpen2] = React.useState(false);
    const [number, setNumber] = React.useState(0);
    const dispatch = useDispatch();
    const dataCart = useSelector(cartData$);
    const [isCheckedDel, setIsCheckedDel] = React.useState(false)
    const [data, setData] = React.useState([])
    const [list, updateList] = useState(dataCart);
    const [tongChiPhi, setTongChiPhi] = React.useState("")
    const getIDAccount = useSelector(_idAccount$);
    const dataUser = useSelector(dataAccount$);

    const [isCheckedPay, setIsCheckedPay] = React.useState(false)
    const checkPayment = useSelector(checkPay$)
    const [isLoading, setIsLoading] = useState(true);
    const [idUser, setidUser] = React.useState("");
    const [tenNguoiDung, setTenNguoiDung] = React.useState("");
    const [sdt, setSDT] = React.useState("");
    const [ngaySinh, setNgaySinh] = React.useState("");
    const [xaPhuong, setXaPhuong] = React.useState("");
    const [quanHuyen, setQuanHuyen] = React.useState("");
    const [tinhTP, setTinhTp] = React.useState("");
    const [diaChiCuThe, setDiaChiCuThe] = React.useState("");
    const [username, setUserName] = React.useState("");
    const [AddressUser, setAddressUser] = React.useState("");

    React.useEffect(() => {
        if (dataUser.length != 0) {
            //lấy tên người dùng
            setTenNguoiDung(dataUser.nameUser);
            setUserName(dataUser.nameUser);
            // lấy ảnh người dùng
            //lấy số điện thoại
            const getSDTUser = dataUser.phoneNumber;
            setSDT("0".concat(getSDTUser.toString().slice(2, 11)));
            // lấy địa chỉ cụ thể
            setXaPhuong(dataUser.xa_phuong);
            setQuanHuyen(dataUser.quan_huyen);
            setTinhTp(dataUser.tinh_tp);
            setDiaChiCuThe(dataUser.specificaddress);
            // lấy địa chỉ cụ thể
            setAddressUser(
                dataUser.specificaddress +
                ", " +
                dataUser.xa_phuong +
                ", " +
                dataUser.quan_huyen +
                ", " +
                dataUser.tinh_tp
            );
        } else {
            console.log("====================================");
            console.log("[dataUserERROR]", dataUser);
            console.log("====================================");
        }
    }, [dataUser]);
    console.log(xaPhuong);
    const [location, setLocation] = useState({
        codeCountries: "VN",
        dataCities: [],
        dataCounties: [],
        dataWards: [],
    });
    const GetSortOrder = (prop) => {
        return function (a, b) {
            if (a[prop] > b[prop]) {
                return 1;
            } else if (a[prop] < b[prop]) {
                return -1;
            }
            return 0;
        };
    };
    location.dataCities.sort(GetSortOrder("name"));
    location.dataCounties.sort(GetSortOrder("name"));
    location.dataWards.sort(GetSortOrder("name"));
    function renderlistCities() {
        if (location.codeCountries == "VN") {
            return location.dataCities.map((item, key) => (
                <MenuItem label={item.name} value={item.code} key={key}>
                    {item.name}
                </MenuItem>
            ));
        }
        return <MenuItem label={"Không có dữ liệu"} value={"noData"} />;
    }

    function renderlistCounties() {
        if (location.codeCountries == "VN") {
            const filteredDataCounties = location.dataCounties.filter((item) => {
                return item.parent_code == tinhTP;
            });
            return filteredDataCounties.map((item, key) => (
                <MenuItem label={item.name} value={item.code} key={key}>
                    {item.name}
                </MenuItem>
            ));
        }
        return <MenuItem label={"Không có dữ liệu"} value={"noData"} />;
    }
    function renderlistWards() {
        if (location.codeCountries == "VN") {
            const filteredDataWards = location.dataWards.filter((item) => {
                return item.parent_code == quanHuyen;
            });
            return filteredDataWards.map((item, key) => (
                <MenuItem label={item.name} value={item.name_with_type} key={key}>
                    {item.name}
                </MenuItem>
            ));
        }
        return <MenuItem label={"Không có dữ liệu"} value={"noData"} />;
    }
    React.useEffect(() => {
        if (isCheckedPay == true) {
            setIsCheckedPay(false)
            setTimeout(() => {
                dispatch(action.getAddNotifition.getAddNotifitionRequest({
                    title: 'Đặt hàng thành công',
                    body: 'Mã đơn hàng của bạn là ' + '61ae24a97c5a2a08d87d7c1c',
                    id_hoadon: '61ae24a97c5a2a08d87d7c1c',
                    id_Account: '61a59db5f3c3df3ba061be4d'
                }));
                setIsLoading(!isLoading)
            }, 1100)
        }
        // console.log(checkPayment);
    }, [checkPayment])

    // setlocation(dataCart.find(function (element) {
    //     return element._id === number;
    // }))
    var sum = 0;
    var tong = 0
    var i;
    for (i = 0; i < dataCart.length; i++) {
        sum = sum + dataCart[i].soLuong * dataCart[i].giaSanPham
        tong = tong + dataCart[i].soLuong
    }
    const handleClickOpen = () => {
        setOpen(true);
    };

    const delItem = (a) => {
        setIsCheckedDel(true)
        console.log(a._id);
        dispatch(action.deleteCart.deleteCartRequest({
            _id: a._id
        }, [dispatch]))

    }
    const minusItem = (a) => {

        dispatch(action.updateCountCart.updateCountCartRequest({
            _id: a._id,
            giaSanPham: a.giaSanPham,
            soLuong: a.soLuong - 1
        }))
    }
    const addItem = (a) => {
        dispatch(action.updateCountCart.updateCountCartRequest({
            _id: a._id,
            giaSanPham: a.giaSanPham,
            soLuong: a.soLuong + 1
        }))
        console.log(a.soLuong);
    }
    const handleClose = () => {
        setOpen(false);
        setOpen2(false);
    };
    const handleAgreeClose = (item) => {
        setIsCheckedDel(true)
        console.log(number);
        dispatch(action.deleteCart.deleteCartRequest({
            _id: number
        }, [dispatch]))
        setOpen(false);
    };
    const handelPays = () => {
        min = Math.ceil(min);
        max = Math.floor(max);
        let random = Math.floor(Math.random() * (max - min) + min);
        // console.log('[Random]', random);
        let randomst = random.toString();
        // let randomship = parseInt(randomst.concat(zero))
        console.log(dataCart);
        dispatch(
            action.getAddPay.getAddPayRequest({
                dataCart,
                nameUser: "Bùi Văn Hải",
                address: "Thị trấn Ea Kar, Ea Kar, Đắk Lắk",
                phoneNumber: 84905190166,
                tongThanhToan: tongChiPhi,
                ship: String(randomst).replace(/\B(?=(\d{3})+(?!\d))/g, '.'),
                type: '1',
                specificaddress: "Khối 4",
                id_Account: '61a59db5f3c3df3ba061be4d'
            }));
        alert("Thanh toán thành công <3 admin sẽ sử lý đơn hàng của bạn ngay")
        dispatch(action.getAddNotifition.getAddNotifitionRequest({
            title: 'Đặt hàng thành công',
            body: 'Mã đơn hàng của bạn là ' + '61ae24a97c5a2a08d87d7c1c',
            id_hoadon: '61ae24a97c5a2a08d87d7c1c',
            id_Account: '61a59db5f3c3df3ba061be4d'
        }));
        setOpen2(false)
    }
    React.useEffect(() => {
        dispatch(action.getCountCart.getCountCartRequest(getIDAccount));
        // console.log('[getIDAccount]', getIDAccount);
        const format = String(sum).replace(/\B(?=(\d{3})+(?!\d))/g, '.')
        if (isCheckedDel == true) {
            setTimeout(() => {
                setIsCheckedDel(false)
                setData(dataCart.data)
            }, 100);
        } else {
            setData(dataCart.data)
        }
        setTongChiPhi(format)
    }, [dispatch, dataCart])
    console.log(dataCart);
    const convertPrice = (e) => {
        return String(e).replace(/\B(?=(\d{3})+(?!\d))/g, '.');
    }
    var min = 1;
    var max = 100;
    const [zero, setzero] = useState("000");
    const giohang = [];
    return (
        <>

            <Dialog
                open={open2}
                TransitionComponent={Transition}
                keepMounted
                onClose={handleClose}
                aria-describedby="alert-dialog-slide-description"
            >
                <DialogTitle>{"Địa chỉ mới?"}</DialogTitle>
                <DialogContent>
                    {/* <DialogContentText id="alert-dialog-slide-description"> */}
                    {/* {location.tenSanPham} */}
                    {/* </DialogContentText> */}
                    <div style={{ display: 'flex' }}>
                        <TextField
                            style={{ width: "100%", marginTop: 10 }}
                            id="outlined-basic"
                            label="Tên người dùng"
                            variant="outlined"
                            value={tenNguoiDung}
                            onChange={(val) => setTenNguoiDung(val.target.value)}
                        // onFocus={onfocusName}
                        />
                        <div style={{ width: '5px' }} />
                        <TextField
                            style={{ width: "100%", marginTop: 10 }}
                            id="outlined-basic"
                            label="Số điện thoại"
                            variant="outlined"
                            value={sdt}
                            onChange={(val) => setSDT(val.target.value)}
                        // onFocus={onfocusSDT}
                        />
                    </div>
                    <div style={{ display: 'flex' }}>
                        <TextField
                            style={{ width: "100%", marginTop: 10 }}
                            id="outlined-basic"
                            label="Tỉnh"
                            variant="outlined"
                            value={tinhTP}
                            onChange={(val) => setTinhTp(val.target.value)}
                        // onFocus={onfocusSDT}
                        />
                        <div style={{ width: '5px' }} />
                        <TextField
                            style={{ width: "100%", marginTop: 10 }}
                            id="outlined-basic"
                            label="Huyện"
                            variant="outlined"
                            value={quanHuyen}
                            onChange={(val) => setQuanHuyen(val.target.value)}
                        // onFocus={onfocusSDT}
                        />
                    </div>
                    <div style={{ display: 'flex' }}>

                        <div style={{ marginTop: "10px" }}>
                            <TextField
                                style={{ width: "100%" }}
                                id="outlined-basic"
                                label="Địa chỉ cụ thể"
                                variant="outlined"
                                value={diaChiCuThe}
                                onChange={(val) => setDiaChiCuThe(val.target.value)}
                            // onFocus={onfocusAdress}
                            />
                            {/* {focusInputAddress ? null : (
                                <div
                                    style={{
                                        fontWeight: "bold",
                                        fontSize: 10,
                                        color: "red",
                                    }}
                                >
                                    Không được để trống!
                                </div>
                            )} */}
                        </div>
                        <div style={{ width: '5px' }} />

                        <TextField
                            style={{ width: "50%", marginTop: 10 }}
                            id="outlined-basic"
                            label="Xã"
                            variant="outlined"
                            value={xaPhuong}
                            onChange={(val) => setXaPhuong(val.target.value)}
                        // onFocus={onfocusSDT}
                        />
                    </div>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleClose}>Không</Button>
                    <Button onClick={handelPays}>Đồng ý</Button>
                </DialogActions>
            </Dialog>
            <Dialog
                open={open}
                TransitionComponent={Transition}
                keepMounted
                onClose={handleClose}
                aria-describedby="alert-dialog-slide-description"
            >
                <DialogTitle>{"Bạn có muốn xóa sản phẩm này?"}</DialogTitle>
                <DialogContent>
                    <DialogContentText id="alert-dialog-slide-description">
                        {/* {location.tenSanPham} */}
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleClose}>Không</Button>
                    <Button onClick={handleAgreeClose}>Đồng ý</Button>
                </DialogActions>
            </Dialog>
            <div style={{
                backgroundImage: "linear-gradient(rgba(255,255,255,0.7), rgba(255,255,255,1)), url('./backGroundDangKy.png')",
                width: '100%',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
                flexDirection: 'column',
                alignContent: 'center'
            }}>
                <div style={{
                    width: '1200px',
                    height: 'auto',
                    margin: '50px 0 50px 0'
                }}>
                    <div className="cart-inner">
                        <div className="left_cart ccart">
                            <div className="cart-inner">
                                <div className="cart_col">
                                    <h4><b>Thanh Toán</b></h4>
                                </div>
                                <div className="ctext-muted">{tenNguoiDung}({sdt})</div>
                                <div className="ctext-muted">{diaChiCuThe},{xaPhuong}, {quanHuyen}, {tinhTP} </div>
                            </div>
                            <div style={{
                                display: 'flex',
                                flexDirection: 'row',
                                width: '100%',
                                justifyContent: 'flex-end',
                                marginTop: '20px'
                            }}>
                                <a style={{
                                    width: '50%',
                                    textAlign: 'left',
                                    borderRight: '1px solid gray'
                                }}>Sản phẩm</a>
                                <a style={{
                                    width: '25%',
                                    textAlign: 'center',
                                    borderRight: '1px solid gray'
                                }}>Số lượng</a>
                                <a style={{
                                    width: '25%',
                                    textAlign: 'center',
                                }}>Tổng giá</a>

                            </div>
                            {dataCart.map((item) =>

                                <div className="cart-inner crow-border">
                                    <div className="cart-inner cmain crow-items">
                                        <div className="cart_col2">
                                            <img class="img-fluid" src={item.img} />
                                        </div>
                                        <div style={{ width: '25%', alignItems: 'center' }} className="cart_col">
                                            <div style={{ textAlign: 'center' }} className="cart-inner">{item.tenSanPham}</div>
                                        </div>
                                        <div style={{ width: '10%', alignItems: 'center' }}></div>
                                        <div style={{ width: '25%', textAlign: 'center' }} className="cart_col">

                                            <a style={{ fontSize: '15px' }} href="#" class="border">{item.soLuong}</a>

                                        </div>
                                        <div style={{ width: '25%', textAlign: 'center' }} className="cart_col">
                                            {convertPrice(item.soLuong * item.giaSanPham)} đ
                                        </div>

                                    </div>
                                </div >
                            )}
                            {/* <Link to='/sanpham' style={{ textDecoration: 'none', color: 'black' }}>
                                <div class="back-to-shop"><a>←</a><span class="text-muted"> Quay lại cửa hàng</span></div>
                            </Link> */}
                            <div className="totals-rows">
                                <div className='back-to-shop'>
                                    <Link to='/sanpham' style={{ textDecoration: 'none', color: 'black' }}>
                                        <div class="back-to-shop" style={{ minWidth: '200px' }}><a>← </a><span class="text-muted">Back to shop</span></div>
                                    </Link>
                                </div>
                                <div className="pay_form">
                                    <div className="r_pay">
                                        <div className="pay_text">
                                            <div className="payt_items">Tổng thanh toán(<a style={{ color: '#009387' }}>{tong}</a> sản phẩm):</div>
                                            <div className='payt_totals'> {tongChiPhi} đồng</div>
                                            <button className="pay_btn" >

                                                <span>Thanh toán</span>
                                            </button>
                                        </div>

                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="left_cart cart" style={{ marginTop: '20px' }}>
                            <MultiItemCrousel />
                        </div>

                    </div>

                </div>
            </div >
            <Footer />
        </>
    )
}
export default PayScreen