import React from "react";
import "../css/Cards.css";
import CardItem from "./CardItem";

import { useDispatch, useSelector } from "react-redux";
import * as action from "../redux/actions";
import { postsState$ } from "../redux/selectors";

function Cards() {
  const dispatch = useDispatch();
  const posts = useSelector(postsState$);

  const [data, setData] = React.useState([])

  React.useEffect(() => {
    dispatch(action.getPosts.getPostsRequest());
  }, [dispatch]);

  React.useEffect(() => {
    if (posts != undefined) setData(posts)
  }, [posts]);

  return (
    <div className="cards" >
      <div className="cah1" style={{
        borderBottom: '1px black solid', padding: '1em', backgroundColor: '#009387', color: '#fff', borderTopLeftRadius: '6px', borderTopRightRadius: '6px'
      }}><h1 style={{ fontSize: '1.2em', lineHeight: '1.5em', fontWeight: '600', }}>Bài viết nổi bật</h1></div>
      <div className="cards__container">
        <div className="cards__wrapper">
          <ul className="cards__items" style={{ width: '100%', paddingTop: '0.75em' }}>
            <div style={{ margin: 'auto', display: 'flex' }}>
              {posts.slice(0, 3).map((post) => (
                <CardItem post={post} />
              ))}
            </div>
          </ul>
          <ul className="cards__items">
          </ul>
        </div>
      </div>
    </div >
  );
}

export default Cards;
