import React from 'react';
import { useState } from 'react';
import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import "./carousel.css";
import { ArrowBackIos, ArrowForwardIos } from "@material-ui/icons";
import { Link } from 'react-router-dom';
import { sanPhamSoldState$ } from '../redux/selectors';

import * as actions from '../redux/actions';
import { useDispatch, useSelector } from "react-redux";
import Carousel from "react-elastic-carousel";
import Button from '@mui/material/Button';
import { AiOutlinePlusCircle, AiOutlineMinusCircle } from 'react-icons/ai';
import { cartData$, sanPhamState$, _idAccount$ } from '../redux/selectors';
import * as action from '../redux/actions';

const PreviousBtn = (props) => {
  console.log(props);
  const { className, onClick } = props;
  return (
    <div className={className} onClick={onClick}>
      <ArrowBackIos style={{ color: "orange", fontSize: "30px" }} />
    </div>
  );
};
const NextBtn = (props) => {
  const { className, onClick } = props;
  return (
    <div className={className} onClick={onClick}>
      <ArrowForwardIos style={{ color: "orange", fontSize: "30px" }} />
    </div>
  );
};

const carouselProperties = {
  prevArrow: <PreviousBtn />,
  nextArrow: <NextBtn />,
  slidesToShow: 4,
  // infinite={false}
  // slidesToScroll={3}
  centerMode: true,
  centerPadding: "30px",
  responsive: [
    {
      breakpoint: 426,
      settings: {
        slidesToShow: 1,
        centerMode: false,
      },
    },
    {
      breakpoint: 769,
      settings: {
        slidesToShow: 3,
        centerMode: false,
      },
    },
    {
      breakpoint: 1025,
      settings: {
        slidesToShow: 4,
        centerMode: false,
        slidesToScroll: 2,
      },
    },
  ],
};

const MultiItemCarousel = ({ danhsachsanpham }) => {
  const dispatch = useDispatch();
  const sanPhamSold = useSelector(sanPhamSoldState$);
  const [dataSP, setdataSP] = React.useState([])
  const getIDAccount = useSelector(_idAccount$);
  const [payInput, setPayInput] = useState(1);
  const [page, setPage] = React.useState(0)
  const changePage = (e) => {
    dispatch(actions.getSanPhamsPage.getSanPhamsPageRequest({
      pagenumber: e.target.outerText - 1
    }, [dispatch]))
    setPage(e.target.outerText - 1)
  }
  React.useEffect(() => {
    dispatch(actions.getSanPhamsSold.getSanPhamsSoldRequest());
  }, [dispatch]);

  React.useEffect(() => {
    if (sanPhamSold != undefined) setdataSP(sanPhamSold)
    console.log("[sanPhamSold]", sanPhamSold);
  }, [sanPhamSold]);
  const convertPrice = (e) => {
    return String(e).replace(/\B(?=(\d{3})+(?!\d))/g, '.');
  }
  const _AddCart = () => {
    dispatch(action.getAddCart.getAddCartRequest({ id_Account: getIDAccount, tenSanPham: danhsachsanpham.name, _id: danhsachsanpham._id, giaSanPham: danhsachsanpham.price, soLuong: parseInt(payInput), tongGiaBan: danhsachsanpham.priceSale * parseInt(payInput), img: danhsachsanpham.img, typeProduct: danhsachsanpham.type, flashSale: danhsachsanpham.flashSale, price: danhsachsanpham.price, priceSale: danhsachsanpham.priceSale }
    ))
  }
  return (
    <div style={{ width: '100%', margin: '0 auto', }} className="carousel">
      <div className="cah1" style={{
        borderBottom: '1px black solid', padding: '1em', backgroundColor: '#009387', color: '#fff', borderTopLeftRadius: '6px', borderTopRightRadius: '6px'
      }}><h1 style={{ fontSize: '1.2em', lineHeight: '1.5em', fontWeight: '600', }}>Sản phẩm bán chạy</h1></div>
      <Slider   {...carouselProperties}>
        {dataSP.map((data) => (
          <div href="/sanpham" style={{
            textAlign: "center",
            textDecoration: "underline",
            textDecorationColor: "white",
            minHeight: "100%",
            borderRadius: '10px',
            boxShadow: '0 4px 5px 0 rgba(0, 0, 0, 0.2), 0 4px 6px 0 rgba(0, 0, 0, 0.19)',

            margin: '0px 32px 0px 32px'
          }}>
            <div class="boxsanpham1" >
              <Link onClick={() => window.location.replace(`/chitietsanpham&id=${data._id}&page=${page}`)}
                style={{ textDecoration: 'none', color: 'black' }}
              >
                {
                  data.flashSale == 1 ? <img src="./saleSP.png" style={{
                    position: 'absolute',
                    zIndex: '1',
                    width: "60px",
                    top: '10px',
                    marginLeft: '10px'
                  }}
                  /> : null
                }
                <div class="imagesanpham1">
                  <img src={data.img[0].image}></img>
                </div>
                <div class="infosanpham1">
                  <div style={{
                    minHeight: '50px',
                    display: 'flex',
                    justifyContent: 'center'
                  }}>
                    <h3 style={{ textDecorationColor: 'underline white' }}>{data.name}</h3>
                  </div>
                  <div class="subInfosanpham1">
                    <strong class="pricesanpham1">
                      Giá: {data.flashSale == 0 ?
                        convertPrice(data.price) + '₫' :
                        <span>{convertPrice(data.price)} ₫</span>}
                      {data.flashSale == 1 ? convertPrice(data.priceSale) + "₫" : null}
                    </strong>
                    <div class="starssanpham1">
                      <i class="fas fa-star"></i>
                      <i class="fas fa-star"></i>
                      <i class="fas fa-star"></i>
                      <i class="fas fa-star"></i>
                      <i class="fas fa-star"></i>
                    </div>
                  </div>

                </div>

              </Link>
              <div style={{
                display: 'flex',
                flexDirection: 'row',
                height: '-webkit-fill-available',
                justifyContent: 'space-between',
                width: '100%'
              }}>
                <h3 style={{
                  fontSize: "13px",
                  display: 'flex',
                  alignItems: 'center'
                }}>Đã bán: {data.sold}</h3>
                <div style={{
                  width: "auto",
                  display: 'flex',
                  flexDirection: 'row',
                  justifyContent: 'center',
                  alignItems: 'center'
                }}>
                  <Button style={{
                    width: '10px',
                    minWidth: '25px',
                    minHeight: '25px',
                    borderRadius: '12.5px'
                  }}>
                    <AiOutlinePlusCircle />
                  </Button>
                  <input defaultValue={0} min={8} max={50} step={1} style={{
                    width: '50%',
                    border: "2px solid transparent",
                    textAlign: 'center',
                    WebkitAppearance: 'none'
                  }} type="number" />
                  <Button style={{
                    width: '10px',
                    minWidth: '25px',
                    minHeight: '25px',
                    borderRadius: '12.5px'
                  }}>
                    <AiOutlineMinusCircle />
                  </Button>
                </div>
                <div class="overlaysanpham1">
                  <Button onClick={_AddCart} style={{
                    width: "20px",
                    height: "40px",
                    borderRadius: "20px",
                    backgroundColor: 'transparent'
                  }}>
                    <img style={{
                      width: "60px",
                      padding: "5px",
                      borderRadius: '5px'
                    }}
                      src="./addCart.png" class="fas fa-shopping-cart" />
                  </Button>
                </div>
              </div>
            </div>

          </div >

        ))}
      </Slider >
    </div >
  );
};


export default MultiItemCarousel;
