import React from 'react';
import { useState } from 'react';
import { sanPhamState$, countPage$, timKiemChecking$, timKiemData$, timKiemCountPage$, textTimKiem$ } from '../redux/selectors';
import { useDispatch, useSelector } from "react-redux";
import * as actions from '../redux/actions';
import Danhsachsanpham from './Danhsachsanpham';

const MultiItem = ({ danhsachsanpham }) => {
    const dispatch = useDispatch();
    const sanPham = useSelector(sanPhamState$);
    const [data, setData] = React.useState([])

    React.useEffect(() => {
        // setData(sanPham)
        dispatch(actions.getSanPhamsPage.getSanPhamsPageRequest({ pagenumber: 0 }));
    }, [dispatch])
    return (
        <div style={{ width: '100%', margin: '0 auto' }}>
            <div className="cah1" style={{
                borderBottom: '1px black solid', padding: '1em', backgroundColor: '#009387', color: '#fff', borderTopLeftRadius: '6px', borderTopRightRadius: '6px'
            }}><h1 style={{ fontSize: '1.2em', lineHeight: '1.5em', fontWeight: '600', }}>Tất cả sản phẩm</h1></div>
            <div className=''>
                <div className="box-containersanpham1tong">
                    <div className="box-containersanpham1" style={{ justifyContent: 'center' }}>
                        {sanPham.map(danhsachsanpham => <Danhsachsanpham danhsachsanpham={danhsachsanpham} />)}
                    </div>
                </div>
            </div>
        </div>
    )
}
export default MultiItem;
